<template>
  <!--  -->
  <div class="mt-20">
    <div class="stat__header">
      <div class="fl">
        <el-tooltip placement="right" effect="light">
          <div slot="content"> 
            <br>
            <br>
            <br> / 
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <div class="fr">
        <router-link to="/statistics/product" :style="{color:color}"></router-link>
      </div>
    </div>
    <div class="stat__body">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="14" :xl="14">
          <line-chart></line-chart>
        </el-col>
        <el-col :xs="24" :sm="24" :md="24" :lg="10" :xl="10">
          <chart-warp></chart-warp>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import lineChart from './lineChart'
import chartWarp from './chartWarp'
export default {
  components: {
    lineChart,
    chartWarp
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  data() {
    return {}
  }
}
</script>





