<template>
  <div class="mt-20">
    <div class="stat__header">
      <div class="fl">
        <el-tooltip placement="right" effect="light">
          <div slot="content">
          UV <br>
          PV<br>
           / <br>
          <br>
          <br>
          <br>
          <br>
           <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <div class="fr">
        <router-link  to="/statistics/pv" :style="{color:color}"></router-link>
      </div>
    </div>
    <div class="stat__body">
     <chart></chart>
    </div>
  </div>
</template>
<script>
import chart from './chart.vue'
export default {
  components: {
    chart
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  data() {
    return {
    }
  }
}
</script>







